}
.block--p .txt-content {
  margin-top: 0 !important;
}
@media screen and (min-width: 1024px) {
  .block--p .row {
    flex-direction: row;
  }
}
@media screen and (max-width: 1023px) {
  .block--p .row {
    flex-direction: column-reverse !important;
  }
}
@media screen and (min-width: 1024px) {
  .block--p .media-left {
    flex-direction: row-reverse;
  }
}
@media screen and (max-width: 1023px) {
  .block--p .media-left {
    flex-direction: column-reverse !important;
  }
}
.block--p .media-block .img-wrap {
  text-align: center;
}